import React, { useState } from "react";
import "./index.css";
import "./search.css"
import arrowDown from '../../images/arrow-down.svg';
import arrowUp from '../../images/arrow-up.svg';
import search from '../../images/Search.svg';

type Category = {
    title: string;
    items: string[];
};

const categories: Category[] = [
    {
        title: "",
        items: [" / ", "", ""],
    },
    {
        title: "",
        items: [
            " ",
            "",
            " ",
            " / ",
            "- /   ",
            "  ",
            "  / ",
            "",
            "",
            " ",
            "",
        ],
    },
    {
        title: "",
        items: [
            " / ",
            " / ",
            "",
            " / ",
            "",
            "",
            "",
            "",
            "",
            "",
            "PR-",
            "",
            "",
            " ",
            " -",
        ],
    },
    {
        title: "",
        items: [
            " ",
            "   ",
            " ",
            "",
            "  ",
            "  ",
        ],
    },
    {
        title: "",
        items: [
            " ",
            "   ",
            "  ",
            " ",
        ],
    },
    {
        title: "",
        items: [
            ""
        ]
    }
];



export const EventMenu = () => {
    const [openStates, setOpenStates] = useState<boolean[]>(
        Array(categories.length).fill(false)
    );

    const toggleIndex = (index: number) => {
        setOpenStates(prev => {
            const newStates = [...prev];
            newStates[index] = !newStates[index];
            return newStates;
        });
    };

    return (
        <div className="menu__container">
            <div className="container">
                <div className="top">
                    <ul>
                        <li className="category-header"></li>
                        <li className="top__down">    </li>
                    </ul>
                    <div className="top__desc__search">
                        <form className="search__form">
                            <input className="search__input" type="text" placeholder=""/>
                            <img src={search} alt={"search"}/>

                        </form>
                        <button className="write-to-us-button"> </button>
                    </div>
                </div>
                {categories.map((category, index) => (
                    <div key={category.title} className="category">
                        <div className="category-header" onClick={() => toggleIndex(index)}>
                            {category.title}
                            <span className="category-icon">

                <img
                    src={openStates[index] ? arrowUp : arrowDown}
                    alt="toggle"
                    className="category-icon"
                />
                </span>
                        </div>
                        {openStates[index] && (
                            <ul className="category-items">
                                {category.items.map(item => (
                                    <li key={item}>{item}</li>
                                ))}
                            </ul>
                        )}
                    </div>
                ))}
            </div>
        </div>
    );
};
