import "./index.css";
import React, {useState} from "react";
import cinema from "../../images/cinema.png";
import concert from "../../images/concert.jpeg";
import theatre from "../../images/theatre.jpeg";
import kids from "../../images/kids.jpeg";
import exhibitions from "../../images/exhibitions.jpeg";
import {MyCalendar} from "../Calendar";
import search from "../../images/Search.svg";
import sport from "../../images/sport.jpeg"
import standup from "../../images/standup.jpeg"
import arrowDown from "../../images/arrow-down.svg";
import arrowUp from "../../images/arrow-up.svg";

interface Tab {
    name: string;
    image: string;
    options: string[];
    dropdown?: Record<string, string[]>;
}

interface Card {
    id: string;
    title: string;
    subtitle: string;
    rating: string;
    image: string;
}

const tabs: Tab[] = [{
    name: "",
    image: cinema,
    options: [
        "",
        "",
        "",
        ""
    ],
    dropdown: {
        "": ["", " 5", "", "", ""],
        "": ["", "", "", "", ""]
    }
},
    {name: "", image: concert, options: ["", "", "", " "], dropdown: {
            "": ["", "", "-", "", ""],
            "": ["", "", "-", "", ""]
        }},
    {name: "", image: theatre, options: ["", "", "", " "], dropdown:{
            "":["MON", " . ", " . ", "", "  "],
            "":["", "", "", "", ""]
        }},
    {name: "", image: kids, options: ["", "", " "], dropdown:{
            "":[" 0  2 ", " 3  6 ", " 7  10 ", " 11 "],
            "":["", "", "", "", ""]
        }},
    {name: "", image: exhibitions, options: ["", ""], dropdown:{
            "":["", "", "", "", ""]
        }},
    {name: "", image: sport, options: ["", ""],dropdown:{
            "":["", "", "", "", ""],
            "":["-", "  ", "", " ", " "]
        }},
    {name: "", image: standup, options: []},
    {name: "", image: exhibitions, options: ["", "", "","", "-"]},
];

const generateCards = (tabName: string, image: string): Card[] =>
    Array.from({length: 6}).map((_, index) => ({
        id: `${tabName}-${index}`,
        title: `${tabName}  ${index + 1}`,
        subtitle: " ",
        rating: (7 + Math.random()).toFixed(1),
        image,
    }));

export const City = () => {
    const [selectedTab, setSelectedTab] = useState<Tab>(tabs[0]);
    const [liked, setLiked] = useState<Record<string, boolean>>({});
    const [openStates, setOpenStates] = useState<Record<string, boolean>>({});
    const [selectedOptions, setSelectedOptions] = useState<Record<string, string>>({});


    const handleLike = (id: string) => {
        setLiked((prev) => ({
            ...prev,
            [id]: !prev[id],
        }));
    };

    const handleToggleMenu = (option: string) => {
        setOpenStates((prevState) => ({
            ...prevState,
            [option]: !prevState[option],
        }));
    };
    const cards = generateCards(selectedTab.name, selectedTab.image);

    return (
        <div className="container">
            <div className="top">
                <ul>
                    <li className="category-header"></li>
                    <li className="top__down">     </li>
                </ul>
                <div className="top__desc__search">
                    <form className="search__form">
                        <input className="search__input" type="text" placeholder=""/>
                        <img src={search} alt={"search"}/>

                    </form>
                    <button className="write-to-us-button"> </button>
                </div>
            </div>
            <div className="tabs">
                {tabs.map((tab) => (
                    <button
                        key={tab.name}
                        className={`tab-button ${selectedTab.name === tab.name ? "active" : ""}`}
                        onClick={() => setSelectedTab(tab)}
                    >
                        {tab.name}
                    </button>
                ))}
            </div>
            <div className="mini_tabs">
                <ul className="mini_tabs_items">
                    {selectedTab.options.map((option) => (
                        <li key={option}>
                            <button
                                className="option-button"
                                onClick={() => handleToggleMenu(option)}
                            >
                                {selectedOptions[option] || option}
                                {selectedTab.dropdown?.[option] && (
                                    <img
                                        src={openStates[option] ? arrowUp : arrowDown}
                                        alt="toggle"
                                        className="category-icon"
                                    />
                                )}
                            </button>
                            {selectedTab.dropdown?.[option] && openStates[option] && (
                                <ul className="dropdown-menu">
                                    {selectedTab.dropdown[option].map((item) => (
                                        <li key={item}>
                                            <button
                                                className="dropdown-item-button"
                                                onClick={() => {
                                                    setSelectedOptions((prev) => ({
                                                        ...prev,
                                                        [option]: item
                                                    }));
                                                    setOpenStates((prev) => ({
                                                        ...prev,
                                                        [option]: false
                                                    }));
                                                }}
                                            >
                                                {item}
                                            </button>
                                        </li>
                                    ))}
                                </ul>
                            )}
                        </li>
                    ))}
                </ul>
            </div>
            <div className="main-section">
                <h1>{selectedTab.name}  </h1>
                <p>   </p>

                <div className="cards">
                    <MyCalendar/>
                    {cards.map((card) => (
                        <div className="card" key={card.id}>
                            <div className="image-wrapper">
                                <img className="city__img" src={card.image} alt="img"/>
                                <div className="rating">{card.rating}</div>
                            </div>
                            <p>{card.title}</p>
                            <span>{card.subtitle}</span>
                            <button
                                className={`like-button ${liked[card.id] ? "liked" : ""}`}
                                onClick={() => handleLike(card.id)}
                            >
                                {liked[card.id] ? "" : ""}
                            </button>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};
